	import {mapActions,mapState,mapMutations} from 'vuex';
	export default {

		data() {
			return {
				parames:{"pageSize":"","pageNum":"","productName":"","pruductId":"","productStatus":""},
				pageSize:10,
				productName:'',
				pageNum:1,
				total:'',
				titleNViewBackground: '',
				swiperCurrent: 0,
				swiperLength: 0,
				carouselList: [],
				goodsList: []
			};
		},
		// 初始化数据
        created(){
            this.fetchData();   
          },
		onLoad() {
			this.loadData();
		},
		computed:{
		     productList(){
		      var proList=this.$store.state.product.getProList;
		      this.total=proList.total;
		      this.pageSize=proList.pageSize;
		      this.pageNum=proList.pageNum;
		      return proList.list;
		    },
			selectProductList(){
			  var proList=this.$store.state.product.getProList;
			  this.total=proList.total;
			  this.pageSize=proList.pageSize;
			  this.pageNum=proList.pageNum;
			  var list=[];
			  for (let ls in proList.list) {
				  if(proList.list[ls].productInfo.select){
			  	list.push(proList.list[ls])}
			  }
			  console.log("精选商品",list)
			  return list;
			},
		    ...mapActions({
		      'getprolist':'product/getProList'
		    })
		    
		  },
		methods: {
			// 初始化数据
			fetchData(){
			      var parames=this.parames;
			      parames.pageSize=this.pageSize;
			      parames.pageNum=this.pageNum;
			      this.$store.dispatch('product/getProList',parames)
			    },
			/**
			 * 请求静态数据只是为了代码不那么乱
			 * 分次请求未作整合
			 */
			async loadData() {
				let carouselList = await this.$api.json('carouselList');
				this.titleNViewBackground = carouselList[0].background;
				this.swiperLength = carouselList.length;
				this.carouselList = carouselList;
				
				let goodsList = await this.$api.json('goodsList');
				this.goodsList = goodsList || [];
			},
			//轮播图切换修改背景色
			swiperChange(e) {
				const index = e.detail.current;
				this.swiperCurrent = index;
				this.titleNViewBackground = this.carouselList[index].background;
			},
			//详情页
			navToDetailPage(item) {
				//测试数据没有写id，用title代替
				
				let id = JSON.stringify(item);
				uni.navigateTo({
					url: `/pages/product/product?id=${id}`
				})
			},
		},

























	}
